import {
    ActionBtn,
    DashboardMockWrap,
    HeroSectionWrap,
    HeroTitle,
    JoinToggle,
    SectionMargin,
    Tag,
    VideoPlayerWrap, VideoText, Wrapper
} from "./styles";
import emojy from './Emoji.png'
import Text from "../../components/ds/Text";
import colors from '../../theme/tokens/colors'
import Button from "../../components/ds/Button";
import ArrowIcon from "../../components/ds/Icons/ArrowIcon";
import dashboard from './dashboard.png'
import {AnimationOnScroll} from "react-animation-on-scroll";
import './styles.scss'
import React from "react";
import heartIcon from './heart.png'
import Noise from './Noise.png'

export default function Hero() {

    return (
        <Wrapper>
            <HeroSectionWrap bg={Noise}>
                <div className={'first'}>
                    <Tag>
                        <img src={emojy}/>
                        Amet minim mollit non deserun non det
                    </Tag>

                    <div className={'d-flex align-items-center'}>
                        <JoinToggle>
                            <div className={'joinWrap'}>
                                Join US
                            </div>

                            <div className={'heartWrap'}>
                                <img src={heartIcon}/>
                            </div>
                        </JoinToggle>
                        <HeroTitle>
                            Financial Security
                        </HeroTitle>
                    </div>


                        <SectionMargin>
                            <Text text={'with Crypto Trading Assistant'}
                                  color={colors.brand.text["90"]}
                                  mdFontSize={'72px'}
                                  xsFontSize={'40px'}
                                  mdFontWeight={'700'}
                                  textAlign={'center'}
                                  letterSpacing={'-0.02em'}
                                  textTransform={'uppercase'}
                            />
                        </SectionMargin>

                    <Text
                        text={'Finomate is the unique ``Price Action`` trading assistant, which helps traders to trade with less risk and more profit.'}
                        color={colors.brand.text["80"]}
                        mdFontSize={'18px'}
                        xsFontSize={'10px'}
                        mdFontWeight={'300'}
                        textAlign={'center'}
                        letterSpacing={'-0.02em'}/>

                    <div className={'d-lg-flex d-sm-block align-items-center justify-content-center my-5'}>
                        <Button
                            text={'Get Started'}
                            icon={<ArrowIcon/>}
                            fontSize={'16px'}
                            xsFontSize={'12px'}
                            xsWidth={'159px'}
                            width={'242px'}
                            height={'56px'}
                            fontWeight={'600'}
                            borderRadius={'28px'}
                            padding={'0px 16px'}
                            className={'mx-3 mb-sm-0 mb-3 d-flex align-items-center justify-content-center primary'}/>

                        <Button
                            text={'Learn More'}
                            fontSize={'16px'}
                            xsFontSize={'12px'}
                            xsWidth={'159px'}
                            width={'242px'}
                            height={'56px'}
                            bgColor={''}
                            fontWeight={'600'}
                            borderRadius={'28px'}
                            padding={'0px 16px'}
                            className={'mx-3 secondary'}/>
                    </div>
                </div>
            </HeroSectionWrap>

            <AnimationOnScroll animateIn="animate__test" duration={6} animateOnce={true}>
                <DashboardMockWrap>
                    <img src={dashboard} className={'testing'}/>
                </DashboardMockWrap>
            </AnimationOnScroll>
        </Wrapper>
    )
}