import {Txt} from "./styles";

export interface Text_Options {
    text: string;
    icon?: any;
    color: string;
    bgColor?: any;
    xsFontSize?: any;
    smFontSize?: any;
    mdFontSize: any;
    lgFontSize?: any;
    xlFontSize?: any;
    xllFontSize?: any;
    xsFontWeight?: any;
    smFontWeight?: any;
    mdFontWeight: any;
    lgFontWeight?: any;
    xlFontWeight?: any;
    xllFontWeight?: any;
    letterSpacing?: any;
    textAlign: string;
    textTransform?: any;
    padding?: any;
    margin?: any;
    borderRadius?: any;
    height?: any;
    cursor?: any;
    className?: any;
    style?: any;
}

export default function Text(props: Text_Options) {
    return (
        <Txt color={props.color}
             bgColor={props.bgColor}
             xsFontSize={props.xsFontSize}
             smFontSize={props.smFontSize}
             mdFontSize={props.mdFontSize}
             lgFontSize={props.lgFontSize}
             xlFontSize={props.xlFontSize}
             xllFontSize={props.xllFontSize}
             xsFontWeight={props.xsFontWeight}
             smFontWeight={props.smFontWeight}
             mdFontWeight={props.mdFontWeight}
             lgFontWeight={props.lgFontWeight}
             xlFontWeight={props.xlFontWeight}
             xllFontWeight={props.xllFontWeight}
             letterSpacing={props.letterSpacing}
             textAlign={props.textAlign}
             textTransform={props.textTransform}
             padding={props.padding}
             margin={props.margin}
             borderRadius={props.borderRadius}
             height={props.height}
             cursor={props.cursor}
             className={props.className}
             style={props.style}>
            {props.text}
        </Txt>
    )
}