import {Btn} from "./styles";

export interface Btn_Options {
    text: string;
    type?: any | 'button';
    status?: false;
    icon?: any;
    color?: any;
    fontSize: any;
    xsFontSize?: any;
    smFontSize?: any;
    lgFontSize?: any;
    xlFontSize?: any;
    xllFontSize?: any;
    height?: any;
    width?: any;
    xsWidth?: any;
    smWidth?: any;
    lgWidth?: any;
    xlWidth?: any;
    xllWidth?: any;
    bgColor?: any;
    borderColor?: any;
    fontWeight?: any;
    letterSpacing?: any;
    borderRadius?: any;
    textTransform?: any;
    transform?: any;
    padding?: any;
    buttonStyleType?: any; //outline | fill | text
    onClick?: () => void;
    className?: any;
}

export default function Button(props: Btn_Options) {
    return (
        <Btn styleType={props.buttonStyleType}
             color={props.color}
             fontSize={props.fontSize}
             xsFontSize={props.xsFontSize}
             smFontSize={props.smFontSize}
             lgFontSize={props.lgFontSize}
             xlFontSize={props.xlFontSize}
             xllFontSize={props.xllFontSize}
             height={props.height}
             width={props.width}
             xsWidth={props.xsWidth}
             smWidth={props.smWidth}
             lgWidth={props.lgWidth}
             xlWidth={props.xlWidth}
             xllWidth={props.xllWidth}
             bgColor={props.bgColor}
             borderColor={props.borderColor}
             fontWeight={props.fontWeight}
             letterSpacing={props.letterSpacing}
             borderRadius={props.borderRadius}
             textTransform={props.textTransform}
             transform={props.transform}
             padding={props.padding}
             onClick={props.onClick}
             type={props.type}
             disabled={props.status} className={props.className}>
            {props.text}
            {props.icon}
        </Btn>
    )
}