import {Plans, Wrapper} from "./styles";
import colors from "../../theme/tokens/colors";
import Text from "../../components/ds/Text";
import Premium from "./icons/premium";
import Extended from "./icons/extended";
import Business from "./icons/business";
import {AnimationOnScroll} from 'react-animation-on-scroll';
import Button from "../../components/ds/Button";
import Lottie from 'react-lottie';
import * as animationData from './animated-svg/arrow-animation.json'

export default function Plan() {
    const defaultOptions = {
        loop: true,
        autoplay: true,
        animationData: animationData,
        rendererSettings: {
            preserveAspectRatio: 'xMidYMid slice'
        }
    };

    const plans = [
        {
            id: '1',
            title: 'Freemium',
            icon: '',
            unit: '',
            price: 'free',
            offPrice: '',
            per: '',
            features: [
                {title: '1 Market'},
                {title: '3 Strategies'},
                {title: '1 Pairs'},
                {title: '1H timeframe'},
                {title: 'Divergance'},
                {title: 'Notifications and alarms'},
            ],
            actionTitle: 'Sign up',
            moreDes: ''
        },
        {
            id: '2',
            title: 'Premium',
            icon: <Premium/>,
            unit: '$',
            price: '49',
            offPrice: '',
            per: '1 month',
            features: [
                {title: 'All markets'},
                {title: 'All strategies'},
                {title: 'More than 80 coins'},
                {title: 'Unlimited pairs'},
                {title: '+12 timeframes'},
                {title: '24/7 support'},
            ],
            actionTitle: 'Choose',
            moreDes: ''
        },
        {
            id: '3',
            title: 'Extended',
            icon: <Extended/>,
            unit: '$',
            price: '49',
            offPrice: '129$',
            per: '3 month',
            features: [
                {title: 'All markets'},
                {title: 'All strategies'},
                {title: 'More than 80 coins'},
                {title: 'Unlimited pairs'},
                {title: '+12 timeframes'},
                {title: '24/7 support'},
            ],
            actionTitle: 'Choose',
            moreDes: ''
        },
        {
            id: '4',
            title: 'Business',
            icon: <Business/>,
            unit: '',
            price: '',
            offPrice: null,
            per: '',
            features: [],
            actionTitle: 'Contact Us',
            moreDes: 'For all of the organizations that need additional features, controls and support.'
        }
    ]
    return (
        <Wrapper>
            <div className={'col-md-12'}>
                <AnimationOnScroll animateIn="animate__fadeIn" duration={4} animateOnce={true}>

                    <Text text={'Pick your premium'}
                          color={colors.brand.text["90"]}
                          mdFontSize={'40px'}
                          xsFontSize={'28px'}
                          mdFontWeight={'700'}
                          textAlign={'center'}
                          textTransform={'uppercase'}
                          margin={'56px 0'}/>

                    <Text text={'Get Finomate’s unlimited assistance and get the most profit in crypto market'}
                          color={colors.brand.text["80"]}
                          mdFontSize={'18px'}
                          xsFontSize={'10px'}
                          mdFontWeight={'300'}
                          textAlign={'center'}
                          margin={'0 0 32px 0'}/>

                </AnimationOnScroll>

                <Plans>
                    <div className={'col-md-12'}>
                        <div className={'row'}>
                            {plans.map((item) => {
                                return (
                                    <div className={'col-md-3'}>
                                        <div className={'row'}>
                                            <div className={'planCart'}>
                                                <div className={'head'}>
                                                    <div className={'type'}>
                                                        {item.icon ?
                                                            <div className={'me-2 d-flex'}>
                                                                {item?.icon}
                                                            </div>
                                                            : ''}

                                                        {item.title}
                                                    </div>
                                                    {item.moreDes ? <Text
                                                            color={colors.brand.text["80"]}
                                                            mdFontSize={'14px'}
                                                            xsFontSize={'10px'}
                                                            mdFontWeight={'300'}
                                                            text={item.moreDes} textAlign={'left'}/> :
                                                        <div className={'d-flex align-items-center'}>
                                                            <div className={'unit'}>{item.unit}</div>
                                                            <div className={'price'}>{item.price}</div>
                                                            <div className={'per'}>
                                                                {item.per ? '/' : ''}
                                                                {item.per}
                                                            </div>

                                                            {item.offPrice ? <div className={'offTag'}>
                                                                {item.offPrice}
                                                            </div> : ''}

                                                        </div>
                                                    }
                                                </div>

                                                <div className={'divider'}/>

                                                <div className={'body-wrap'}>
                                                    {item.title === 'Business' ?
                                                        <div className={'body'}>
                                                            <Text text={'Let’s talk!'}
                                                                  color={colors.brand.text["90"]}
                                                                  mdFontSize={'32px'}
                                                                  xsFontSize={'28px'}
                                                                  mdFontWeight={'700'}
                                                                  textAlign={'center'} margin={'33px auto 17px auto'}/>
                                                            <Lottie options={defaultOptions}
                                                                    height={197}
                                                                    width={97}/>
                                                        </div>
                                                        :

                                                        <div className={'body'}>
                                                            {item?.features.map((subItem) => {
                                                                return (
                                                                    <div className={'each-feature'}>
                                                                        {subItem?.title}
                                                                    </div>
                                                                )
                                                            })}
                                                        </div>
                                                    }

                                                    <Button text={item.actionTitle}
                                                            bgColor={colors.brand.primary.main}
                                                            borderColor={colors.brand.primary.main}
                                                            height={'56px'}
                                                            width={'240px'}
                                                            padding={'0px 64px'}
                                                            borderRadius={'28px'}
                                                            color={colors.brand.text["90"]}
                                                            className={'d-block mt-auto mx-auto'}
                                                            fontSize={'16px'}
                                                            fontWeight={'600'}
                                                            buttonStyleType={'fill'}/>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })}
                        </div>
                    </div>
                </Plans>
            </div>
        </Wrapper>
    )
}