import {HelpItem, Wrapper} from "./styles";
import Text from "../../components/ds/Text";
import colors from "../../theme/tokens/colors";
import Scan from "./icons/scan";
import Radar from "./icons/radar";
import Notification from "./icons/notification";
import MedalStar from "./icons/medalStar";
import {AnimationOnScroll} from "react-animation-on-scroll";
import React from "react";


export default function Four() {
    const features = [
        {
            icon: <Scan/>,
            title: 'Scan crypto market 24/7 for you',
            animation: 'animate__rotateInDownLeft2',
        },
        {
            icon: <Radar/>,
            title: 'Finding price-action positions',
            animation: 'animate__rotateInDownRight2',
        },
        {
            icon: <Notification/>,
            title: 'Realtime alert to trader',
            animation: 'animate__rotateInDownLeft2',
        },
        {
            icon: <MedalStar/>,
            title: 'Check opportunity and set order',
            animation: 'animate__rotateInDownRight2',
        },
    ]

    return (
        <Wrapper>
        <div className={'col-md-12'}>
            <Text text={'How To Use Finomate ?'}
                  color={colors.brand.text["90"]}
                  mdFontSize={'40px'}
                  xsFontSize={'28px'}
                  mdFontWeight={'700'}
                  textAlign={'center'}
                  textTransform={'uppercase'}
                  margin={'56px 0'}/>
            <div className={'col-md-12'}>
                <div className={'row'}>
                    <div className={'col-md-3'}></div>
                    <div className={'col-md-6'}>
                        {features.map((item, i) => {
                            return (
                                <AnimationOnScroll animateIn={item.animation} duration={0.5 + i} animateOnce={true}>
                                    <HelpItem>
                                        <div className={'iconHolder'}>
                                            {item.icon}
                                        </div>

                                        <Text text={item.title}
                                              color={colors.brand.text["90"]}
                                              mdFontSize={'24px'}
                                              xsFontSize={'14px'}
                                              mdFontWeight={500}
                                              textAlign={'left'}
                                              textTransform={'uppercase'}/>
                                    </HelpItem>
                                </AnimationOnScroll>
                            )
                        })}
                    </div>
                    <div className={'col-md-3'}></div>
                </div>
            </div>
        </div>
        </Wrapper>
    )
}