export const JoinToggle = styled.div`
  width: 200px;
  padding: 4px;
  height: 62px;
  border-radius: 200px;
  background-color: #2B3036;
  opacity: 0.8;
  background-size: 10px 10px;
  background-image: repeating-linear-gradient(-45deg, #0E0F11 0, #2B3036 1px, #2b3036 0, #39394e 50%);
  position: relative;

  &:hover {
    .joinWrap {
      visibility: hidden;
    }

    .heartWrap {
      visibility: visible;
    }
  }

  @media screen and (max-width: 576px) {
    display: none;
  }

  .joinWrap {
    padding: 4px;
    width: 101px;
    height: 46px;
    border: 4px solid #3A3E46;
    background: #000000;
    border-radius: 200px;
    font-weight: 700;
    font-size: 12px;
    line-height: 18px;
    text-align: center;
    color: #ECEDEE;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 4px;
    transition: all 100ms ease-in-out;
  }

  .heartWrap {
    background: #F4F7F8;
    border-radius: 200px;
    width: 72px;
    height: 46px;
    padding: 4px;
    border: 4px solid #3A3E46;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 4px;
    visibility: hidden;
    transition: all 100ms ease-in-out;
    animation-delay: 105ms;
  }
`

