export const Plans = styled.div`
  .planCart {
    height: 619px;
    background: #151719;
    margin-bottom: 60px;
    border-left: 1px solid #3A3E46;
    border-top: 1px solid #3A3E46;
    border-bottom: 1px solid #3A3E46;
    padding: 0;
    @media screen and (max-width: 576px) {
      margin-bottom: 0;
      border-bottom: unset;
      height: 100%;
      padding-bottom: 30px;
    }

    .head {
      position: relative;
      padding: 24px 32px;
      @media screen and (max-width: 576px) {
        padding: 16px 32px;
      }

      .type {
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 500;
        font-size: 32px;
        line-height: 50px;
        letter-spacing: -0.02em;
        color: #FFFFFF;
        display: flex;
        align-items: center;
        @media screen and (max-width: 576px) {
          font-size: 24px;
        }
      }

      .unit {
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 600;
        font-size: 28px;
        line-height: 40px;
        letter-spacing: -0.02em;
        color: #676B70;
        @media screen and (max-width: 576px) {
          font-size: 14px;
        }
      }

      .price {
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 600;
        font-size: 44px;
        line-height: 64px;
        letter-spacing: -0.02em;
        color: #FFFFFF;
        @media screen and (max-width: 576px) {
          font-size: 28px;
          line-height: 48px;
        }
      }

      .per {
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 28px;
        letter-spacing: -0.02em;
        color: #676B70;
        @media screen and (max-width: 576px) {
          font-size: 12px;
        }
      }

      .offTag {
        padding: 2px 12px;
        gap: 8px;

        width: 58px;
        height: 28px;

        /* State/Error/Dark/30 */

        background: #2E292E;
        border-radius: 16px;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        /* identical to box height, or 150% */

        letter-spacing: -0.02em;
        text-decoration-line: line-through;

        /* State/Error/Dark/50-Main */

        color: #E54350;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }

    .body-wrap {
      min-height: 70%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
      @media screen and (max-width: 576px) {
        min-height: 55%;
      }
    }

    .body {
      padding: 16px 30px;

      .each-feature {
        position: relative;
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 28px;
        letter-spacing: -0.02em;
        color: #ECEDEE;
        padding-left: 32px;
        margin-bottom: 16px;
        @media screen and (max-width: 576px) {
          font-size: 14px;
        }

        &:after {
          content: '';
          width: 24px;
          height: 24px;
          background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M18.5305 7.46983C18.8233 7.76281 18.8232 8.23768 18.5302 8.53049L10.5255 16.5305C10.3847 16.6711 10.1939 16.7501 9.99496 16.75C9.79601 16.7499 9.60525 16.6708 9.46465 16.53L5.46936 12.53C5.17664 12.237 5.17692 11.7621 5.46998 11.4694C5.76305 11.1766 6.23792 11.1769 6.53064 11.47L9.99576 14.9392L17.4698 7.46951C17.7628 7.17671 18.2377 7.17685 18.5305 7.46983Z' fill='%233C9059'/%3E%3C/svg%3E%0A");
          position: absolute;
          left: 0;
          z-index: 10;
        }
      }
    }

    .divider {
      background: #3A3E46;
      border-radius: 1px;
      width: 100%;
      height: 1px;
    }
  }

  .col-md-12 .row .col-md-3 {
    &:first-child {
      .planCart {
        border-radius: 24px 0 0 24px;

        @media screen and (max-width: 576px) {
          border-radius: 24px 24px 0 0;
        }
      }
    }

    &:last-child {
      .planCart {
        border-radius: 0 24px 24px 0;
        border-right: 1px solid #3A3E46;
        @media screen and (max-width: 576px) {
          border-radius: 0 0 24px 24px;
        }
      }
    }
  }
`
