export const TimeLine = styled.div`
  position: relative;

  .col-md-6:first-child {
    padding-top: 160px;
    @media screen and (max-width: 576px) {
      padding-top: 30px;
    }
  }

  .col-md-6:nth-child(odd) {
    .box {
      margin-top: 60px;
      margin-bottom: 60px;
      @media screen and (max-width: 576px) {
        margin-top: 80px;
        margin-bottom: 80px;
      }
    }
  }

  .col-md-6:nth-child(even) {
    .box {

    }
  }

  &:after {
    content: '';
    background: #FFFFFF;
    height: 100%;
    width: 1px;
    position: absolute;
    top: 0;
    left: 50%;
    @media screen and (max-width: 576px) {
      content: unset;
    }
  }

  .box {
    padding: 0px 40px;
    gap: 30px;
    width: 404px;
    height: 444px;
    background: #0C71E4;
    border-radius: 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    @media screen and (max-width: 576px) {
      width: 100%;
    }

    .avatarHolder {
      width: 78px;
      height: 78px;

      background: #FFFFFF;
      /* Surface/100 */

      border: 1px solid #FFFFFF;
      border-radius: 493.534px;
      display: flex;
      align-items: center;
      justify-content: center;

      img {
        width: 50px;
        height: 50px;
      }
    }
  }
`


