export const ActionBtn = styled.div`
  .action {
    transform: rotate(-8deg);
    transition: all 200ms ease-in-out;

    &:hover {
      transform: rotate(0deg);
    }
  }
`


export const VideoPlayerWrap = styled.div`
  width: 192px;
  height: 192px;
  background: #2B3036;
  border: 0.5px solid #000000;
  border-radius: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: revert;
  .circle{
    position: absolute;
    animation-name: rotate;
    animation-duration: 15s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
  }
  @keyframes rotate{
    from{ transform: rotate(-360deg); }
    to{ transform: rotate(360deg); }
  }
  
  .innerCircle {
    width: 96px;
    height: 96px;
    background: linear-gradient(141.25deg, #2B3036 13.26%, #3A3E46 85.47%);
    border: 0.5px solid #000000;
    border-radius: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
`

export const VideoText = styled.div`
  .text {
    font-family: 'Handlee' !important;
  }
`


export const SecondImgWrap = styled.div`
  position: relative;
  height: 100%;
  display: flex;
  align-items: center;
  @media screen and (max-width: 576px) {
    height: 300px;
    padding: 40px 0;
  }

  .img1 {
    perspective: 100px;
    perspective-origin: -90%;

    position: absolute;
    right: -468px;
    top: 22px;
    height: 50vh;
    z-index: 1;

    @media screen and (max-width: 576px) {
      right: -259px;
      top: -22px;
      height: 78%;
    }

    img {
      height: 100%;
      width: 100%;
      transform-style: preserve-3d;
      transform: skew(16deg, -9deg);
    }
  }

  .img2 {
    perspective: 100px;
    perspective-origin: -90%;
    position: absolute;
    right: -430px;
    top: 0px;
    height: 50vh;
    opacity: 0.3;
    @media screen and (max-width: 576px) {
      right: -234px;
      height: 78%;
    }

    img {
      height: 100%;
      width: 100%;
      transform-style: preserve-3d;
      transform: skew(16deg, -9deg);
      transform: matrix(0.96, -0.15, 0.29, 0.99, 0, 0);
    }
  }

`