import {CustomerLayout, TimeLine, Wrapper} from "./styles";
import StarRating from 'react-svg-star-rating'
import avatar1 from './avatar/avatar1.png'
import avatar2 from './avatar/avatar2.png'
import avatar3 from './avatar/avatar3.png'
import avatar4 from './avatar/avatar4.png'
import Text from "../../components/ds/Text";
import colors from "../../theme/tokens/colors";
import {AnimationOnScroll} from "react-animation-on-scroll";

export default function Customers() {
    const comments = [
        {
            rate: 4,
            des: 'You do not need to check charts all day long! With Finomate you can walk away from your computer once in a while; let your trading assistant do the hard work without breaking a sweat.',
            avatar: avatar1,
            username: 'Sadegh Sadeghi - PD @Google',
            animation: 'animate__rotateInDownLeft',
        },
        {
            rate: 5,
            des: 'Finomate makes me no longer need to take the time to find price action situations because Fino do it for me',
            avatar: avatar2,
            username: 'hadi Ahmadian - UX Designer @Microsoft',
            animation: 'animate__rotateInDownRight',
        },
        {
            rate: 5,
            des: 'I never thought that a trading assistant could be so useful and profitable. For me, trading is divided into before and after Finomate.',
            avatar: avatar3,
            username: 'Ehsan Abbasi - PM  @Finomate',
            animation: 'animate__rotateInDownLeft',
        },
        {
            rate: 3,
            des: 'With Finomate, I am confident that I will not miss any position in the market, and it feels very good to a trader.',
            avatar: avatar4,
            username: 'Fatemeh Zafarpor- PD @Apple',
            animation: 'animate__rotateInDownRight',
        },
    ]
    return (
        <CustomerLayout>
            <AnimationOnScroll duration={2} animateOnce={true}>
                <Wrapper className={'test'}>
                    <div className={'col-md-12'}>
                        <Text text={'Our customesrs are our biggest fans.'}
                              color={colors.brand.text["90"]}
                              mdFontSize={'40px'}
                              xsFontSize={'28px'}
                              mdFontWeight={'700'}
                              textAlign={'center'}
                              textTransform={'uppercase'}
                              margin={'56px 0'}/>

                        <Text
                            text={'Amet minim mollit Amet minim mollit non deserun non det Amet minim mollit non deserun non d non det non deserun non det'}
                            color={colors.brand.text["80"]}
                            mdFontSize={'18px'}
                            xsFontSize={'10px'}
                            mdFontWeight={'300'}
                            textAlign={'center'}
                            className={'des'}
                            margin={'0 0 120px 0'}/>

                        <TimeLine>
                            <div className={'col-md-12'}>
                                <div className={'row'}>
                                    {comments.map((item) => {
                                        return (
                                            <div className={'col-md-6 col-sm-12'}>
                                                <AnimationOnScroll animateIn={item.animation} duration={2}
                                                                   animateOnce={true}>
                                                    <div className={'box'}>
                                                        <StarRating isReadOnly initialRating={item.rate} unit="half"/>
                                                        <Text text={item.des} color={colors.brand.text["90"]}
                                                              mdFontSize={'16px'}
                                                              xsFontSize={'14px'}
                                                              mdFontWeight={'300'} textAlign={'center'}/>
                                                        <div className={'avatarHolder'}>
                                                            <img src={item.avatar} alt={'avatar image'}/>
                                                        </div>

                                                        <Text text={item.username}
                                                              color={colors.brand.text["90"]}
                                                              mdFontSize={'12px'}
                                                              xsFontSize={'10px'}
                                                              mdFontWeight={'500'} textAlign={'center'}
                                                              margin={'17px 0 0 0'}/>

                                                    </div>
                                                </AnimationOnScroll>
                                            </div>
                                        )
                                    })}
                                </div>
                            </div>
                        </TimeLine>
                    </div>
                </Wrapper>
            </AnimationOnScroll>
        </CustomerLayout>
    )
}