import {Banner, FAQList, Wrapper} from "./styles";
import Text from "../../components/ds/Text";
import colors from "../../theme/tokens/colors";
import React from "react";
import bannerBG from '../../assets/images/Illustration.png'
import Button from "../../components/ds/Button";

export default function FAQ() {
    const faq = [
        {
            id: 1,
            question: 'What is Finomate?',
            answer: 'Amet minim mollit Amet minim mollit non deserun non det Amet minim mollit non deserun non d non det non deserun non det Amet minim mollit Amet minim mollit non deserun non det Amet minim mollit non deserun non d non det non deserun non det Amet minim mollit Amet minim mollit non deserun non det Amet minim mollit non deserun non d non det non deserun non det '
        },
        {id: 2, question: 'What does trading assistand  mean?', answer: 'answer2'},
        {id: 3, question: 'How Can I upgrade my acoount to premium?', answer: ''},
        {id: 4, question: 'How Can I upgrade my acoount to premium? ', answer: ''},
        {id: 5, question: 'How Can I upgrade my acoount to premium? ', answer: ''},
        {id: 6, question: 'How Can I trade with Finomate?', answer: ''},
    ]

    const [collapse, setCollapse] = React.useState<number>(0)
    return (
        <>
            <Wrapper>
                <div className={'col-md-12'}>
                    <Text text={'Frequently Asked Questions'}
                          color={colors.brand.text["90"]}
                          mdFontSize={'40px'}
                          xsFontSize={'28px'}
                          mdFontWeight={'700'}
                          textAlign={'center'}
                          textTransform={'uppercase'}
                          margin={'56px 0'}/>

                    <Text
                        text={'We Happy to answer your question!'}
                        color={colors.brand.text["80"]}
                        mdFontSize={'18px'}
                        xsFontSize={'10px'}
                        mdFontWeight={'300'}
                        textAlign={'center'}
                        margin={'0 0 32px 0'}/>
                    <FAQList>
                        {faq.map((item) => {
                            return (
                                <div className={'each-line'}>
                                    <div className={'d-flex align-items-center justify-content-between'}
                                         onClick={(e) => {
                                             if (item.id === collapse) {
                                                 setCollapse(0)
                                             } else {
                                                 setCollapse(item.id)
                                             }
                                         }}>
                                        <Text
                                            text={item.question}
                                            color={colors.brand.text["90"]}
                                            mdFontSize={'18px'}
                                            xsFontSize={'10px'}
                                            mdFontWeight={'500'}
                                            textAlign={'left'}
                                            cursor={'pointer'}/>
                                        <svg width="16" height="16" viewBox="0 0 16 16" fill="none"
                                             xmlns="http://www.w3.org/2000/svg">
                                            <path
                                                d="M11.2004 7.99999C11.2004 8.46665 11.0204 8.93332 10.6671 9.28665L6.32042 13.6333C6.12708 13.8267 5.80708 13.8267 5.61375 13.6333C5.42042 13.44 5.42042 13.12 5.61375 12.9267L9.96042 8.57999C10.2804 8.25999 10.2804 7.73999 9.96042 7.41999L5.61375 3.07332C5.42042 2.87999 5.42042 2.55999 5.61375 2.36665C5.80708 2.17332 6.12708 2.17332 6.32042 2.36665L10.6671 6.71332C11.0204 7.06665 11.2004 7.53332 11.2004 7.99999Z"
                                                fill="#828D9C"/>
                                        </svg>
                                    </div>

                                    {collapse === item.id &&
                                        <Text
                                            text={item.answer}
                                            color={colors.brand.text["70"]}
                                            mdFontSize={'14px'}
                                            xsFontSize={'10px'}
                                            mdFontWeight={'300'}
                                            textAlign={'left'}
                                            margin={'28px 0 0 0'}/>
                                    }
                                </div>
                            )
                        })}
                    </FAQList>
                </div>
            </Wrapper>

            <Banner style={{backgroundImage: `url(${bannerBG}`}}/>

            <div className={'pb-5'}>
                <Text text={'Do you need aditional assistance?'} color={colors.brand.text["90"]} mdFontSize={'32px'}
                      xsFontSize={'24px'} mdFontWeight={'500'} textAlign={'center'}/>
                <Text text={'Our friendly suppor team is here to help.'} color={'#B0B3B5'} mdFontSize={'18px'}
                      xsFontSize={'10px'} mdFontWeight={'500'} textAlign={'center'}/>

                <Button text={'Contact Us'}
                        color={colors.brand.text["90"]}
                        fontSize={'16px'}
                        xsFontSize={'12px'}
                        buttonStyleType={'fill'}
                        bgColor={colors.brand.primary.main}
                        borderRadius={'28px'}
                        borderColor={colors.brand.primary.main}
                        padding={'0px 64px'}
                        height={'56px'}
                        className={'mt-4 mb-4 mx-auto d-block'}/>
            </div>
        </>
    )
}